    }

}

table {
    border:none;
    border-collapse: collapse;
}

table th {
    padding: 8px;
}

table td {
    border-left: 1px solid #000;
    border-right: 1px solid #000;
    padding: 8px;
}

table td:first-child {
    border-left: none;
}

table td:last-child {
    border-right: none;
}